import React, { Component } from 'react';
import { Survey, ListSurvey, ViewSurvey } from './survey';
import { HashRouter, Route, Switch } from 'react-router-dom';
import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
            <h3 className="App-title">Fedo</h3>
        </header>
        <div className="App-body">
            <HashRouter>
                <Switch>
                    <Route exact path="/" component={ListSurvey} />
                    <Route exact path="/surveys" component={ListSurvey} />
                    <Route exact path="/surveys/new" component={Survey} />
                    <Route exact path="/surveys/:surveyKey" component={ViewSurvey} />
                </Switch>
            </HashRouter>
        </div>
      </div>
    );
  }
};
export default App;




