import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import SurveyApi from './SurveyApi.js';
import './ListSurvey.css';

class ListSurvey extends Component {
    constructor(props) {
        super(props);
        this.state = {
            surveys: []
        };    
    }

    componentDidMount() {
        SurveyApi.list().then((function(data) {
            this.setState({surveys: data});
        }).bind(this));
    }

    render() {
        var surveyUI = [];
        for(var i=0; i < this.state.surveys.length; i++) {
            var s = this.state.surveys[i];
            surveyUI.push(<li key={s.surveyKey}><Link to={"/surveys/" + s.surveyKey }>{s.title}</Link></li>);
        }
    
        var listView = (<ul>{surveyUI}</ul>);
        if (!surveyUI.length) {
            listView = (<p className="nodata">No surveys yet.</p>)
        }
        return (
            <div className="ListSurvey">
                <div className="ListSurvey-header clearfix">
                    <h3>Surveys</h3>
                    <Link className="fa fa-plus-circle newSurveyBtn" to="/surveys/new" />
                </div>
                <div className="ListSurvey-body">
                    {listView}
                </div>
            </div>
        );
    }

}

export default ListSurvey;



