import React, { Component } from 'react';
import './style.css';

class FileUpload extends Component {
    state = { formId: null }
    inputFileBox = { value: "" }
    constructor(props) {
        super(props);
        var formId = "FileUpload_form_" + (new Date()).getTime();
        this.state = { formId: formId};
    }

    render() {
        var fileFieldName = this.props.fileFieldName || "file";
        var btnClass = this.props.btnClass;
        var btnText = this.props.btnText;
        var className = "FileUpload ";
        if (this.props.className) {
            className += this.props.className;
        }
        return (
            <div className="FileUpload">
                <a href="#" className={btnClass} onClick={this.chooseAndUpload.bind(this)}>{btnText}</a>
                <form action={this.props.url}
                    id={ this.state.formId }
                    method="POST"
                    encType="multipart/form-data"
                    encoding="multipart/form-data">
                    <input  type="file" name={ fileFieldName } id={ fileFieldName } />
                </form>
            </div>
        );
    }

    onLoad(e) {
        var onLoad = this.props.onLoad || function() {};
        var data;
        try {
            var doc = e.target.contentWindow.document;
            console.log(doc);
            data = JSON.parse(doc.getElementsByTagName('pre')[0].innerHTML);
        } catch (e) { 
            console.log(e);
        } finally {
            // remove iframe
            if (this.iframeId) {
                var iframe = document.getElementById(this.iframeId);
                if (iframe) iframe.parentNode.removeChild(iframe);
            }
        }
      
        this.inputFileBox.value = "";
        if (data) { 
            onLoad(data);
        }
    }

    chooseAndUpload(e) {
        e.preventDefault();
        this.iframeId = "FileUpload_iframe_" + (new Date()).getTime();
        var iframe = document.createElement('iframe');
        var fileFieldName = this.props.fileFieldName || "file";
        var form = document.getElementById(this.state.formId),
            file = form[fileFieldName];

        iframe.name = this.iframeId;
        iframe.id = this.iframeId;
        iframe.width = 0;
        iframe.height = 0;
        document.body.appendChild(iframe);
        // ignore first load (i.e. adding iframe before registring
        // onload listener)
        iframe.onload = this.onLoad.bind(this);
        form.target = this.iframeId;

        this.inputFileBox = file;
        file.onchange = function(e) {
            e.target.value && e.target.value.length && form && form.submit();
        };

        file.click();
    }
}


export default FileUpload;



