import React from 'react';

class SurveyApi {
   static list () {
        return fetch('/api/surveys/', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            }
        }).then((res) => res.json());
   }

   static getSurvey(surveyKey) {
       return fetch('/api/surveys/' + surveyKey, {
           method: 'GET',
           headers: {
               'Content-Type': 'application/json',
               'Accept': 'application/json'
           }
       }).then((res) => res.json());
   }
}

export default SurveyApi;



