import React, {Component} from 'react';
import ReactTable from 'react-table';
import SurveyApi from './SurveyApi.js';
import 'react-table/react-table.css';


const TABLE_COLUMNS = [
    {accessor: 'emailId', Header: 'Email'},
    {accessor: 'rating', Header: 'Rating'},
    {accessor: 'feedbacktext', Header: 'Comments'},
    {accessor: 'status', Header: 'Status'}
];

class ViewSurvey extends Component {
    state = { survey: {} }
    componentWillMount() {
        SurveyApi.getSurvey(this.props.match.params.surveyKey).then((function(data) {
            this.setState({survey: data});
        }).bind(this))    
    }

    render() {
        return (
            <div>
                <h3>Feedbacks</h3>
                <ReactTable
                    columns={TABLE_COLUMNS}
                    data={this.state.survey.feedbacks}
                    defaultPageSize={10}
                />
            </div>
         );
    }
}

export default ViewSurvey;



