import React, { Component } from 'react';
import RichEditor from '../editor';
import FileUpload from '../fileupload';
import './style.css';

class Survey extends Component {
  state = {recepients: [] }
  editorContent = { }
  render() {
    return (
        <div className="MailEditor">
            <ListImportCSV url="/api/recepients/import"
                fileFieldName="recepients"
                onLoad = { this.onLoad.bind(this) }
                btnClass = "fa fa-file uploadBtn"
                className = "RecepientBox"
                placeholder = "To: (Import from csv)"
                />
            <input type="text" placeholder="Subject" onChange={this.onSubjectChange.bind(this)}/>
            <RichEditor onChange={this.editorChanged.bind(this)}/>
            <i className="fa fa-paper-plane sendBtn" onClick={this.sendSurvey.bind(this)} />
        </div>
    );
  }

  onSubjectChange(e) {
    this.state.mailSubject = e.target.value;
    this.setState(Object.assign({}, this.state));
  }

  onLoad(data) {
      this.state.recepients = data.recepients;
      this.setState(Object.assign({}, this.state));
  }

  sendSurvey() {
      fetch('/api/mail/sendSurvey', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'text/plain'
        }, 
        body: JSON.stringify({
            editorContent: this.editorContent,
            recepients: this.state.recepients,
            subject: this.state.mailSubject
        })
      })
      .then((resp) => resp.text())
      .then((function(data) {
        alert(data);
        this.props.history.push('/surveys/');
      }).bind(this));
  }

  editorChanged(editorContent) {
    this.editorContent = editorContent;
  }
}

class ListImportCSV extends Component {
    state = { list: [] }
    id = (new Date()).getTime()
    render() {
        return (
            <div className={this.props.className}>
                <input id={this.id} type="text" readOnly="true" placeholder={ this.props.placeholder }
                    value={this.state.list} />
                <FileUpload url={this.props.url}
                    fileFieldName={this.props.fileFieldName}
                    onLoad = { this.onLoad.bind(this) }
                    btnClass = { this.props.btnClass }
                    />
            </div>
        );
    }

    onLoad(data) {
        if (data && data.recepients) {
            this.setState({list: data.recepients});
            
            // workaround to clear placeholder text
            setTimeout((function() {
                var elem = document.getElementById(this.id);
                if (elem) { elem.click(); }
            }).bind(this), 0);
        }
        if (this.props.onLoad && typeof this.props.onLoad === 'function') {
            this.props.onLoad(data);
        }
    }
};

export default Survey;



