import React, { Component } from 'react';
import './style.css';

class Rating extends Component {
    render() {
        var stars = [],
            maxRating = this.props.max || 5,
            enableTextFeedback = this.props.feedbackBox || true,
            group = "star" + (new Date()).getTime();
        for(var i=1; i <= maxRating; i++) {
            var key = "star_" + (new Date()).getTime() + "_" + i;
            stars.push(<Star key={key} rating={i} max={maxRating} group={group} />);
        }

        var textbox = null;
        if (enableTextFeedback) {
            textbox = <FeedbackBox />;       
        }
        return (
            <form name="feedbackForm" id="feedbackForm"
                action="#" method="POST">
                <div className="rating theme-default">
                    {stars}
                    {textbox}
                </div>
                <p><button type="submit">Submit feedback</button></p>      
            </form>
        );
    }
}

class Star extends Component {
    render() {
        var id = this.props.group + "_" + this.props.rating;
        var stars = ["☆", "★"];
        var text = "";
        for(var i=0; i < this.props.max; i++) {
            if (i < this.props.rating) text += stars[1];
            else text += stars[0];
        }
        return (
            <div>
                <input type="radio" name={this.props.group}
                    id={id} value={this.props.rating}/>
                <label htmlFor={id}>{text}</label>
            </div>
        );
    }
}

class FeedbackBox extends Component {
    render() {
        return (
                <div className="feedbackBox">
                <textarea name="feedbackBox" id="feedbackBox" placeholder="We would please to have your comments..."></textarea>      
                </div>
        );
    }
}
export default Rating;



