'use strict';

import React from 'react';
import { Editor, EditorState, RichUtils, AtomicBlockUtils, convertToRaw} from 'draft-js';
import Rating from '../rating';
import 'draft-js/dist/Draft.css';
import 'font-awesome/css/font-awesome.min.css';
import './style.css';

class RichEditor extends React.Component {
    constructor(props) {
      super(props);
      this.state = {editorState: EditorState.createEmpty()};

      this.focus = () => this.refs.editor.focus();
      this.onChange = (editorState) => { this.setState({editorState});
                        if (this.props.onChange) {
                            this.props.onChange(convertToRaw(editorState.getCurrentContent()));
                        } 
                      };

      this.handleKeyCommand = (command) => this._handleKeyCommand(command);
      this.onTab = (e) => this._onTab(e);
      this.toggleBlockType = (type) => this._toggleBlockType(type);
      this.toggleInlineStyle = (style) => this._toggleInlineStyle(style);
      this.onEmbed = (object) => this._onEmbed(object);
    }

    _handleKeyCommand(command) {
      const {editorState} = this.state;
      const newState = RichUtils.handleKeyCommand(editorState, command);
      if (newState) {
        this.onChange(newState);
        return true;
      }
      return false;
    }

    _onTab(e) {
      const maxDepth = 4;
      this.onChange(RichUtils.onTab(e, this.state.editorState, maxDepth));
    }

    _toggleBlockType(blockType) {
      this.onChange(
        RichUtils.toggleBlockType(
          this.state.editorState,
          blockType
        )
      );
    }

    _toggleInlineStyle(inlineStyle) {
      this.onChange(
        RichUtils.toggleInlineStyle(
          this.state.editorState,
          inlineStyle
        )
      );
    }

    _onEmbed(object) {
        const {editorState} = this.state;
        const contentState =  editorState.getCurrentContent();
        const contentStateWithEntity = contentState.createEntity(
            'rating',
            'IMMUTABLE',
            null      
        );

        const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
        const newEditorState = EditorState.set(editorState, { currentContent: contentStateWithEntity});

        this.onChange(
                AtomicBlockUtils.insertAtomicBlock(newEditorState, entityKey, '')
                );
    }

    render() {
      const {editorState} = this.state;

      // If the user changes block type before entering any text, we can
      // either style the placeholder or hide it. Let's just hide it now.
      let className = 'RichEditor-editor';
      var contentState = editorState.getCurrentContent();
      if (!contentState.hasText()) {
        if (contentState.getBlockMap().first().getType() !== 'unstyled') {
          className += ' RichEditor-hidePlaceholder';
        }
      }

      return (
        <div className="RichEditor-root">
          <BlockStyleControls
            editorState={editorState}
            onToggle={this.toggleBlockType}
          />
          <InlineStyleControls
            editorState={editorState}
            onToggle={this.toggleInlineStyle}
          />
          <EmbedControls
            editorState={editorState}
            onEmbed={this.onEmbed}
            />
          <div className={className} onClick={this.focus}>
            <Editor
              blockStyleFn={getBlockStyle}
              blockRendererFn={getBlockRenderer}
              customStyleMap={styleMap}
              editorState={editorState}
              handleKeyCommand={this.handleKeyCommand}
              onChange={this.onChange}
              onTab={this.onTab}
              placeholder="Write something nice..."
              ref="editor"
              spellCheck={true}
            />
          </div>
        </div>
      );
    }
}

// Custom overrides for "code" style.
const styleMap = {
    CODE: {
      backgroundColor: 'rgba(0, 0, 0, 0.05)',
      fontFamily: '"Inconsolata", "Menlo", "Consolas", monospace',
      fontSize: 16,
      padding: 2,
    },
};

function getBlockStyle(block) {
    switch (block.getType()) {
      case 'blockquote': return 'RichEditor-blockquote';
      default: return null;
    }
}

function getBlockRenderer(block) {
    switch(block.getType()) {
        case 'atomic': return {
                                component: Rating, 
                                props: { feedbackBox: true }
                        };
        default: return null;
    }
}

class StyleButton extends React.Component {
    constructor() {
      super();
      this.onToggle = (e) => {
        e.preventDefault();
        this.props.onToggle(this.props.style);
      };
    }

    render() {
      let className = 'RichEditor-styleButton';
      if (this.props.active) {
        className += ' RichEditor-activeButton';
      }

      return (
        <span className={className} onMouseDown={this.onToggle}>
          {this.props.label}
        </span>
      );
    }
}

const BLOCK_TYPES = [
    {label: 'H1', style: 'header-one', key:'H1'},
    {label: 'H2', style: 'header-two', key: 'H2'},
    {label: 'H3', style: 'header-three', key: 'H3'},
    {label: 'H4', style: 'header-four', key: 'H4'},
    {label: 'H5', style: 'header-five', key: 'H5'},
    {label: 'H6', style: 'header-six', key: 'H6'},
    {label: <i className="fa fa-quote-left" />, style: 'blockquote', key: 'Blockquote'},
    {label: <i className="fa fa-list-ul" />, style: 'unordered-list-item', key: 'UL'},
    {label: <i className="fa fa-list-ol" />, style: 'ordered-list-item', key: 'OL'},
    {label: <i className="fa fa-code" />, style: 'code-block', key: 'Code Block'},
];

const BlockStyleControls = (props) => {
    const {editorState} = props;
    const selection = editorState.getSelection();
    const blockType = editorState
      .getCurrentContent()
      .getBlockForKey(selection.getStartKey())
      .getType();

    return (
      <div className="RichEditor-controls">
        {BLOCK_TYPES.map((type) =>
          <StyleButton
            key={type.key}
            active={type.style === blockType}
            label={type.label}
            onToggle={props.onToggle}
            style={type.style}
          />
        )}
      </div>
    );
};

var INLINE_STYLES = [
    {label: <i className="fa fa-bold" />, style: 'BOLD', key:'Bold'},
    {label: <i className="fa fa-italic" />, style: 'ITALIC', key: 'Italic'},
    {label: <i className="fa fa-underline" />, style: 'UNDERLINE', key: 'Underline'},
    /*{label: 'Monospace', style: 'CODE'},*/
];

const InlineStyleControls = (props) => {
    var currentStyle = props.editorState.getCurrentInlineStyle();
    return (
      <div className="RichEditor-controls">
        {INLINE_STYLES.map(type =>
          <StyleButton
            key={type.key}
            active={currentStyle.has(type.style)}
            label={type.label}
            onToggle={props.onToggle}
            style={type.style}
          />
        )}
      </div>
    );
};

var EMBED_TYPES = [
    {label: <i className="fa fa-star-half-empty RichEditor-embed-controls" />, type: 'RATING'}
];

const EmbedControls = (props) => {
    return (
        <div className="RichEditor-controls RichEditor-embed-controls">
            {EMBED_TYPES.map(type =>
              <StyleButton
                key={type.label}
                active={false}
                label={type.label}
                onToggle={props.onEmbed}
                style={type.type}
              />)}
        </div>
    );
}
export default RichEditor;



